@import (less) "color.less";
.nbp-modal {
    display: none;
    position: fixed;
    z-index: 10000;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
     &.no-static-backdrop__small{
      width: 34.375em;
    }
    &.container__medium{
      width: 64em;
    }
    &.container__large{
      width: 85.375em;
    }
}

.nbp-modal-title {
    color: #323232;
    font-size: 18px;
    padding-top:20px
}

.nbp-modal-sub-title {
    text-transform: capitalize;
    color: #323232;
    padding-top:20px
}

.close-icon-container {
  position: relative;
  & > .ico-azioni-elimina{
    position: absolute;
    right:20px;
    top:20px;
    z-index: 10;
    color: @green;
    cursor: pointer;
  }
}

.nbp-modal-body-with-title {
  padding: 20px;
}

.nbp-modal-body-without-title {
  padding: 40px;
}

.nbp-modal-container {
    background-color: #fefefe;
    margin: auto;
    padding: 0px 20px 20px 20px;
    border: 1px solid #888;
    &.container__small{
      max-width: 34.375em;
      min-width: 17.375em;
    }
    &.container__medium{
      max-width: 64em;
      min-width: 32em;
    }
    &.container__large{
      max-width: 85.375em;
      min-width: 42.375em;
    }
}

.nbp-close-modal {
    color: #aaaaaa;
    font-size: 28px;
    font-weight: bold;
      &:hover,
      &:focus {
        color: #000;
        text-decoration: none;
        cursor: pointer;
      }
}
